import QStabs from '../../../components/QS-tabs/QS-tabs.vue';
export default {
	components:{
		QStabs
	},
	data() {
		return {
			ipt: '',	//搜索默认值
			// autograph:'', //用户签名
			nickName : '', //用户姓名
			headImage: '',  //用户头像
			piclist:[], //相册图片数组
			tabsChoice:['热门路线','精选路线','最新发布'],//选择标题
			currentText:0,//标题下标
			touristRoute : [], //景点内容
			filterIndex : 0,//tabs默认值
			loadingType: 'more', //加载更多状态
		}
	},
	
	//返回接口数据调用
	onLoad() {
		this.lunBoInit();
		this.loadData();
	},
	
	methods: {
		//读取静态页面json.js
		async lunBoInit(){
			let lunBo = await this.$api.lyfwcwd('lunBo');
			this.piclist = lunBo.image;
			let tourist_route = await this.$api.lyfwcwd('touristRoute');
			this.touristRoute = tourist_route;
		},
		
		//搜索事件
		searchNow: function(e) {
			if (this.ipt == '') {
				uni.showToast({
					title: '未输入搜索关键字',
					icon: 'none',
					duration: 1000
				});
				// return false;
			}
			uni.showToast({
				title:'你搜索了'+this.ipt,
				icon:'none',
			})
			this.ipt = ''
		}, 
		
		change(index){
			this.currentText = index;
			this.filterIndex = index;
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.loadData('refesh',1);
		},
		
		//加载信息，带下拉刷新和上滑刷新加载
		async loadData(type='add',loading){
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more';
			}
			
			let touristRoute = await this.$api.lyfwcwd('touristRoute');
			if(type === 'refresh'){
				this.touristRoute = [];
			}
			
			//筛选，测试数据直接前端筛选了
				if(this.filterIndex === 0){
					touristRoute.sort((a,b)=>a.like - b.like)
				}
				if(this.filterIndex === 1){
					touristRoute.sort((a,b)=>a.routeId - b.routeId)
				}
				if(this.filterIndex === 2){
					touristRoute.sort((a,b)=>a.date < b.date ? 1:-1)
				} 
				// this.scenicSpot = this.scenicSpot.concat(scenicSpot);
				
				// 判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
				this.loadingType = this.touristRoute.length > 20 ? 'nomore' : 'more';
				if(type === 'refresh'){
					if(loading == 1){
						uni.hideLoading()
					}else{
						uni.stopPullDownRefresh();
					}
				}
			},
			
			//景点内容点击
			godetail : function (e){
				uni.showToast({
					title:'你点击了'+e,
					icon:'none'
				})
				setTimeout(function(){
					uni.navigateTo({
						url:'touristroute'
					})
				},500);
			},
	}
}
